.plan {
	padding: 142px 0 75px;
	background-image: url("../img/right-plan-bg.png");
	background-size: 100% auto;
	background-position: center top 80px;
	background-repeat: no-repeat;
	@include sm {
		background-position: center top;
		background-size: auto;
		padding: 80px 0 30px; }
	&__title {
		margin-bottom: 47px;
		@include xmd {
			margin-bottom: 25px; } }
	&__subtitle {
		max-width: 714px;
		margin: 0 auto 60px;
		color: $white;
		text-align: center;
		@include f(null, 500, 1.56);
		@include sm {
			margin-bottom: 32px; } }
	&__box {
		margin-bottom: 95px;
		@include xmd {
			margin-bottom: 0; }
		@include sm {
			padding-bottom: 68px; }
		.slick-dots {
			@include sm {
				bottom: -68px; } } }
	&__label {
		text-align: center;
		@include f(null, 500, 1.56);
		@include xmd {
			display: none; }
		&-link {
			color: #0B8CD4;
			@include f(null, 700, null);
			text-decoration: none;
			@include hover {
				text-decoration: underline; } } } }
