}

.slick-dots li {
  display: inline-block;
  vertical-align: top;
  padding: 5px;
}

.slick-dots li.slick-active button {
  background: #0696D4;
}

.slick-dots li button {
  font-size: 0;
  padding: 0;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: none;
  background-color: rgba(6, 150, 212, 0.5);
  transition: background .3s;
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  border-radius: 100px;
  color: #fff;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.05);
  padding: 16px 21px 15px;
  font-family: "Montserrat";
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
}

.no-touch .btn:hover:before {
  transform: translateX(-50%);
}

.btn:before {
  content: "";
  z-index: 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 400%;
  height: 100%;
  border-radius: 100px;
  background: linear-gradient(90deg, #5AD7FF, #33DAFF, #01A0D3, #0168d3);
  transition: transform .3s;
  will-change: transform;
}

.btn__in {
  position: relative;
  z-index: 1;
}

.btn_long {
  padding-left: 38px;
  padding-right: 38px;
}

.btn-nav {
  position: absolute;
  top: 50%;
  right: 26px;
  transform: translateY(-50%);
  background: none;
  padding: 15px 11px 16px;
  font-size: 0;
  color: #fff;
  display: none;
}

@media only screen and (max-width: 1160px) {
