}

.btn-close {
  position: absolute;
  right: 26px;
  top: 25px;
  background: none;
  padding: 0;
  line-height: 0;
  color: #fff;
  font-size: 20px;
  padding: 15px;
  transition: transform .3s;
  display: none;
}

