.header {
	position: fixed;
	z-index: 100;
	top: 0;
	left: 0;
	width: 100%;
	padding: 30px 0;
	transform: translateY(5px);
	transition: padding .3s, transform .3s;
	@include lg {
		padding: 20px 0; }
	@include sm {
		padding: 10px 0;
		transform: translateY(10px); }
	&.is-active {
		transform: translateY(0);
		.header__bg {
			&:before {
				transform: translateX(-75%); } }
		.header__mobile-menu {
			@include md {
				transform: translateY(0); } }
		.btn-close {
			@include md {
				transform: translateY(-5px); }
			@include sm {
				transform: translateY(-10px); } } }
	&__bg {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		overflow: hidden;
		&:before {
			content: "";
			z-index: -1;
			position: absolute;
			left: 0;
			top: 0;
			width: 400%;
			height: 400%;
			background: linear-gradient(112.74deg, rgba(0,0,0,0), rgba(0,0,0,0), #01A0D3 , #0168d3);
			transition: transform .3s;
			will-change: transform; } }
	&__inner {
		position: relative;
		max-width: 1920px;
		padding-left: 45px;
		padding-right: 45px;
		@include lg {
			padding-left: 30px;
			padding-right: 30px; }
		@include md {
			padding-right: 93px; }
		@include sm {
			padding-left: 20px;
			padding-right: 60px; } }
	&__btn {
		float: right;
		margin-left: 13px;
		@include sm {
			display: none; } }
	&__logo {
		float: left;
		position: relative;
		z-index: 1001;
		@include sm {
			padding: 11px 0; }
		&-in {
			@include sm {
				font-size: 28px; } } }
	&__mobile-menu {
		float: right;
		@include md {
			float: none;
			position: fixed;
			z-index: 1000;
			left: 0;
			top: 0;
			right: 0;
			height: 100vh;
			padding: 90px 0 30px;
			overflow: auto;
			min-width: 320px;
			opacity: 0;
			visibility: hidden;
			background: linear-gradient(112.74deg, #01A0D3 , #0168d3);
			transform: translateY(-5px);
			transition: opacity .3s,visibility .3s, transform .3s;
			@include vertical; }
		@include sm {
			transform: translateY(-10px);
			padding: 70px 0 30px; }
		&.is-open {
			opacity: 1;
			visibility: visible; } } }

.menu {
	font-size: 0;
	@include md {
		display: inline-block;
		vertical-align: middle; }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 0 15px;
		@include md {
			display: block;
			padding: 15px; } }
	&__link {
		display: block;
		padding: 17px 10px;
		text-transform: uppercase;
		text-decoration: none;
		color: $white;
		@include f(16px, 500, 1); } }
