.contacts {
	padding: 142px 0 55px;
	background: #F6FAFD;
	@include xmd {
		padding-bottom: 8px; }
	@include sm {
		padding-top: 80px; }
	&__title {
		margin-bottom: 26px;
		@include sm {
			margin-bottom: 12px; } }
	&__subtitle {
		max-width: 380px;
		margin: 0 auto 88px;
		text-align: center;
		color: #808284;
		@include f(null, 600, 1.44);
		@include xmd {
			margin-bottom: 78px; }
		@include sm {
			max-width: 290px;
			margin-bottom: 30px;
			@include f(14px, null, null); } }
	&__inner {
		max-width: 680px; } }
