.works {
	padding: 142px 0 62px;
	text-align: center;
	@include sm {
		padding: 90px 0 0; }
	&__inner {
		max-width: 1200px; }
	&__title {
		margin-bottom: 65px;
		@include sm {
			margin-bottom: 45px; } }
	&__subtitle {
		max-width: 605px;
		margin: 0 auto 70px;
		color: #73ADCC;
		@include f(null, null, 1.44);
		@include xmd {
			margin-bottom: 90px; }
		@include sm {
			margin-bottom: 45px; } }
	&__description {
		margin-top: 167px;
		min-height: 205px;
		padding: 62px 0;
		background-image: url("../img/slice.png");
		background-size: cover;
		background-position: center center;
		@include f(18px, null, 1.5);
		@include xmd {
			margin-top: 30px;
			min-height: 100px;
			padding: 30px 0; }
		@include sm {
			font-size: 16px;
			padding: 0 15px;
			margin-top: 70px; }
		@include xs {
			padding: 0;
			font-size: 14px; } }
	&__container {
		position: relative;
		min-height: 610px;
		@include xmd {
			min-height: auto; } }
	&__phone {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		line-height: 0;
		@include xmd {
			display: none; }
		&-in {
			width: 100%;
			height: 100%; } }
	&__slider {
		display: flex;
		flex-wrap: wrap;
		@include xmd {
			padding: 0 20px; }
		@include sm {
			padding: 0;
			display: block; }
		&-item {
			padding-top: 53px;
			flex-basis: 50%;
			max-width: 50%;
			@include xmd {
				padding: 0 30px 50px; }
			@include sm {
				padding: 0;
				flex-basis: 100%;
				max-width: 100%; }
			&:nth-child(odd) {
				padding-right: 152px;
				text-align: right;
				@include xmd {
					padding-right: 30px;
					text-align: left; }
				@include sm {
					padding: 0 0 50px;
					text-align: center; }
				.works__item {
					padding-right: 87px;
					@include lg {
						padding-right: 60px; }
					@include xmd {
						padding-right: 0; }
					&-img {
						margin-left: auto;
						@include xmd {
							margin-left: 0; }
						@include sm {
							margin-left: auto;
							margin-right: auto; }
						&-line {
							left: calc(100% - 4px);
							&:after {
								right: 0; }
							&:before {
								left: 0; } } } } }
			&:nth-child(even) {
				padding-left: 152px;
				text-align: left;
				@include xmd {
					padding-left: 30px; }
				@include sm {
					padding: 0 0 50px;
					text-align: center; }
				.works__item {
					padding-left: 87px;
					@include lg {
						padding-left: 60px; }
					@include xmd {
						padding-left: 0; }
					&-img {
						margin-right: auto;
						@include sm {
							margin-left: auto;
							margin-right: auto; }
						&-line {
							right: calc(100% - 4px);
							&:after {
								left: 0; }
							&:before {
								right: 0; } } } } } } }
	&__item {
		&-img {
			position: relative;
			width: 60px;
			height: 60px;
			margin-bottom: 8px;
			@include vertical;
			@include sm {
				margin-bottom: 14px; }
			&-in {
				display: inline-block !important;
				vertical-align: middle;
				max-width: 100%;
				max-height: 100%; }
			&-line {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				width: 73px;
				height: 2px;
				background: #0C8BD5;
				@include xmd {
					display: none; }
				&:before,
				&:after {
					content: "";
					position: absolute;
					top: -3px;
					width: 8px;
					height: 8px;
					border-radius: 50%; }
				&:before {
					border: 2px solid $white;
					background: #0C8BD5; }
				&:after {
					border: 2px solid #0C8BD5;
					background: $white; } } }
		&-title {
			margin-bottom: 4px;
			@include f(18px, 600, 1.22);
			@include sm {
				margin-bottom: 17px; } }
		&-value {
			color: rgba(78, 78, 80, 0.7);
			@include f(null, 500, null); } } }
